/** Media Queries **/
html {
  font-size: 82.5%;
  box-sizing: border-box;
  height: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

/*  mantiene fija la cabecera de la tabla  */
thead.sticky {
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 1;
}